/* src/Game.css */
.game {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #333; /* สีพื้นหลังของหน้าจอ */
  color: #f5f5f5; /* สีข้อความ */
  font-family: 'Arial', sans-serif;
  padding: 20px; /* เพิ่ม padding เพื่อให้เนื้อหาไม่ติดขอบหน้าจอ */
  box-sizing: border-box;
}

.game-board {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px; /* เพิ่มช่องว่างระหว่างแถว */
}

.square {
  width: 150px;
  height: 150px;
  background: #444; /* สีพื้นหลังของปุ่ม */
  border: 2px solid #666; /* สีขอบของปุ่ม */
  color: #fff; /* สีข้อความภายในปุ่ม */
  font-size: 2.5rem; /* ใช้ rem เพื่อให้ขนาดตัวอักษรปรับตามขนาดของหน้าจอ */
  font-weight: bold;
  line-height: 100px; /* ทำให้ข้อความอยู่ตรงกลาง */
  text-align: center;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s, border-color 0.3s;
}

.square:hover {
  background-color: #555; /* สีพื้นหลังของปุ่มเมื่อเอาเมาส์ไปชี้ */
  border-color: #777; /* สีขอบของปุ่มเมื่อเอาเมาส์ไปชี้ */
}

.board-row {
  display: flex;
}

.status {
  text-align: center;
  font-size: 1.5rem; /* ใช้ rem เพื่อให้ขนาดข้อความปรับตามขนาดของหน้าจอ */
  margin-bottom: 10px;
}

.reset-button {
  padding: 12px 24px;
  font-size: 1rem; /* ใช้ rem เพื่อให้ขนาดตัวอักษรปรับตามขนาดของหน้าจอ */
  font-weight: bold;
  color: #fff;
  background-color: #007bff; /* สีพื้นหลังของปุ่ม */
  border: none;
  border-radius: 8px; /* มุมปุ่มมน */
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* เงาของปุ่ม */
}

.reset-button:hover {
  background-color: #0056b3; /* สีพื้นหลังเมื่อเอาเมาส์ไปชี้ */
  transform: translateY(-2px); /* เลื่อนปุ่มขึ้นเล็กน้อย */
}

.reset-button:active {
  background-color: #004494; /* สีพื้นหลังเมื่อกดปุ่ม */
  transform: translateY(0); /* ปลดการเลื่อนปุ่มขึ้น */
}

@media (max-width: 600px) {
  .square {
    width: 80px; /* ขนาดปุ่มเมื่อหน้าจอแคบลง */
    height: 80px;
    font-size: 1.5rem; /* ขนาดข้อความลดลง */
    line-height: 80px;
  }

  .status {
    font-size: 1.2rem; /* ขนาดข้อความลดลง */
  }

  .reset-button {
    padding: 10px 20px;
    font-size: 0.9rem; /* ขนาดข้อความลดลง */
  }
}

@media (max-width: 400px) {
  .square {
    width: 60px; /* ขนาดปุ่มเมื่อหน้าจอแคบมาก */
    height: 60px;
    font-size: 1rem; /* ขนาดข้อความลดลง */
    line-height: 60px;
  }

  .status {
    font-size: 1rem; /* ขนาดข้อความลดลง */
  }

  .reset-button {
    padding: 8px 16px;
    font-size: 0.8rem; /* ขนาดข้อความลดลง */
  }
}
